/**
* Reset some basic elements
*/
body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure {
  margin: 0;
  padding: 0;
}



/**
* Basic styling
*/
body {
  font-family: $base-font-family;
  font-size: $base-font-size;
  line-height: $base-line-height;
  font-weight: $font-weight-normal;
  color: $text-color;
  background-color: $background-color;
  -webkit-text-size-adjust: 100%;
}



/**
* Set `margin-bottom` to maintain vertical rhythm
*/
h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
ul, ol, dl, figure,
%vertical-rhythm {
  margin-bottom: $spacing-unit / 2;
}

/**
* Images
*/
img {
  max-width: 100%;
  vertical-align: middle;
}



/**
* Figures
*/
figure > img {
  display: block;
}

figcaption > img {
  text-align: center;
  font-size: $tiny-font-size;
}



/**
* Lists
*/
ul, ol {
  margin-left: $spacing-unit;
}

li {
  > ul,
  > ol {
    margin-bottom: 0;
  }
}



/**
* Headings
*/
h1, h2, h3, h4, h5, h6 {
  font-weight: $font-weight-bold;
  line-height: 1.35;
}



/**
* Links
*/
a {
  color: $brand-primary-color;
  transition: color .3s;
  text-decoration: none;

  &:hover {
    color: $text-color;
    text-decoration: none;
  }
}



/**
* Blockquotes
*/
blockquote {
  color: $grey-color;
  border-left: 4px solid $grey-color-light;
  padding-left: $spacing-unit / 2;
  font-size: 18px;
  font-style: italic;

  > :last-child {
    margin-bottom: 0;
  }
}



/**
* Code formatting
*/
pre,
code {
  color: #1e1e1e;
  font-size: 15px;
  border: 1px solid #ddd;
  border-radius: 3px;
  background-color: #f8f8f8;
}

code {
  padding: 2px 7px;
}

pre {
  padding: 8px 12px;
  overflow-x: scroll;

  > code {
    border: 0;
    padding-right: 0;
    padding-left: 0;
  }
}



/**
* Wrapper
*/
.wrapper {
  max-width: -webkit-calc(1000px - (#{$spacing-unit} * 2));
  max-width:         calc(1000px - (#{$spacing-unit} * 2));
  margin-right: auto;
  margin-left: auto;
  padding-right: $spacing-unit;
  padding-left: $spacing-unit;
  @extend %clearfix;

  @include media-query($on-palm) {
    max-width: -webkit-calc(700px - (#{$spacing-unit}));
    max-width:         calc(700px - (#{$spacing-unit}));
    padding-right: $spacing-unit;
    padding-left: $spacing-unit;
  }
}

.wrapper-navbar {
  max-width: 100vw;
  padding-right: $spacing-unit;
  padding-left: $spacing-unit;
  @extend %clearfix;

  @include media-query($on-palm) {
    max-width: 100vw;
    padding-right: $spacing-unit;
    padding-left: $spacing-unit;
  }
}

/**
* Clearfix
*/
%clearfix {

  &:after {
    content: "";
    display: table;
    clear: both;
  }
}



/**
* Icons
*/
.icon {

  > svg {
    display: inline-block;
    width: 16px;
    height: 16px;
    vertical-align: middle;

    path {
      fill: $grey-color;
    }
  }
}

/**
* Credits
*/
.credits {
  color: $grey-color;
  font-style: italic;
  font-size: $small-font-size;
}
